@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700');

body {
  margin: 0;
  
}

@font-face {
  font-family: "NeueRegular";
  src: local(webfontkit-20221123-143102), 
  url(./webfontkit-20221123-143102/ppneuemachina-inktrapregular-webfont.woff) format("woff");
}

html{
  height: 100%;
}
/* ########################## GRID SETTINGS ############################# */
/* ########################## PAGE 1 ############################# */
.pg-1, .pg-2, .pg-7 {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(9, 1fr);
  height: 100vh;
  width: 100%;
  background-color: rgb(240, 220, 220);
} 

.pg-2{
  background-color: rgb(124, 160, 149);
}

.pg-7 {
  background-color: rgb(35, 33, 48);
}

/* page template */

.header-wrapper{
  grid-column: 1 / 13;
  grid-row: 1 / 2;
  background-color: rgba(0, 81, 255, 0);
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
} 

/* header wrapper */

.name-wrapper{
  grid-column: 1 / 7;
  grid-row: 2 / 4;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  
  /* justify-content: space-between;
  align-items: center; */
  
} 

/* name wrapper */

.screen-wrapper{
  grid-column: 4 / 10;
  grid-row: 4 / 7;
  background-color: rgb(0, 0, 0);
  color: #fff;
  display: flex;
  /* justify-content: space-between;
  align-items: center; */
  padding: 0 2rem;
} 

/* screen wrapper */

.tech-wrapper{
  grid-column: 1 / 4;
  grid-row: 6 / 8;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  display: flex;
  /* justify-content: space-between;
  align-items: center; */
  padding: 0 2rem;
} 

/* tech wrapper */

.title-wrapper{
  grid-column: 8 / 13;
  grid-row: 8 / 10;
  background-color: rgba(0, 0, 0, 0);
  color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
} 

/* title wrapper */

.anim-wrapper{
  grid-column: 1 / 8;
  grid-row: 8 / 10;
  background-color: rgba(255, 255, 255, 0);
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  overflow-y: hidden;
  
  
} 

/* ########################## PAGE 2 ############################# */

.label-wrapper{
  grid-column: 2 / 6;
  grid-row: 3 / 5;
  background-color: rgba(146, 68, 68, 0);
  color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
} 

.skills-wrapper{
  grid-column: 6 / 13;
  grid-row: 3 / 5;
  background-color: rgba(0, 0, 0, 0);
  color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
} 

.lang-wrapper{
  grid-column: 5 / 7;
  grid-row: 6 / 8;
  background-color: rgba(0, 0, 0, 0);
  color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
} 

.tech-wrapper{
  grid-column: 7 / 13;
  grid-row: 5 / 7;
  background-color: rgba(0, 0, 0, 0);
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
} 

.icons-wrapper{
  grid-column: 7 / 13;
  grid-row: 7 / 10;
  background-color: rgba(255, 0, 0, 0);
  color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
} 
/* anim wrapper */



/* ########################## ANIMATION BLOCKS ############################# */
/* ########################## PAGE 1 ############################# */

.anim-box1{
  
  height: 10%;
  color: rgba(0, 0, 0, 0);
  background-color: rgb(0, 0, 0);
  
}

.anim-box2{
  
  height: 8%;
  color: rgba(0, 0, 0, 0);
  background-color: rgb(0, 0, 0);
  
}

.anim-box3{
  
  height: 6%;
  color: rgba(0, 0, 0, 0);
  background-color: rgb(0, 0, 0);
  
}

.anim-box4{
  
  height: 5%;
  color: rgba(0, 0, 0, 0);
  background-color: rgb(0, 0, 0);
  
}

.anim-box5{
  
  height: 4%;
  color: rgba(0, 0, 0, 0);
  background-color: rgb(0, 0, 0);
  
}

/* ########################## TEXT SETTINGS ############################# */

.name-wrapper h1{
  font-family: "NeueRegular";
  font-size: 5vw;
  font-weight: 400;
  color: rgb(0, 0, 0);
  text-align: left;
  text-transform: uppercase;
  padding: 0 2rem;
  padding-top: 2vh;
}

.name-wrapper h2{
  font-family: "NeueRegular";
  font-size: 1.3vw;
  font-weight: 200;
  color: rgb(49, 69, 160);
  text-align: left;
  text-transform: uppercase;
  padding: 0 2rem;
  padding-top: 2vh;
  max-width: 80%;
}


.title-wrapper h3{
  font-family: "NeueRegular";
  font-size: 5vw;
  text-align: right;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: rgb(0, 0, 0);
  /* text-shadow: 0 0 0.5rem rgb(255, 255, 255); */

}

